<template>
  <div class="adddirver">
    <head-top title="Add Devices" go-back="true"></head-top>
    <div class="outleticon">
      <img src="../../../assets/addevice/outleticon@3x.png" >
    </div>
    <div class="prompt">
      <span>Turn on the power and press the socket button for 5 seconds until the net indicator flashes quickly.</span>
    </div>
    <div class="light" @click="clickOut=!clickOut">
      <img v-show="!clickOut" src="../../../assets/addevice/defunct@3x.png" />
      <img v-show="clickOut" src="../../../assets/addevice/click@3x.png" />
      <span>The net indicator flashes  quickly</span>
    </div>
    <div class="nextbtn" :class="{nextDisable: !clickOut}" @click="next">
      <span>Next</span>
    </div>
    <transition :name="transitiondre?'slide-right':'slide-left'" mode="in-out">
      <router-view></router-view>
    </transition>

  </div>
</template>
<script>
import { mapState } from 'vuex'
import { isConnectToWiFiRequest } from '../../../api/method.js'
import { isConnectToWiFiResponse } from '../../../api/getData.js'
import headTop from '../../../components/common/head'

export default {
  data () {
    return {
      clickOut: false // 是否勾选
    }
  },

  mounted () {
    // 判断手机是否连接wifi返回
    isConnectToWiFiResponse(this.NAVIGATOR, this)
  },

  components: {
    headTop
  },

  computed: {
    ...mapState([
      'transitiondre'
    ])
  },

  methods: {
    next () {
      // 勾选后
      if (this.clickOut) {
        if (this.$root.ISDEBUG) {
          window.isConnectToWiFiResponse(true)
        }
        // 判断手机是否连接wifi
        isConnectToWiFiRequest(this.NAVIGATOR, this)
      }
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../../styles/mixin.less';
 .adddirver{
   .fs;
   background: #ffffff;
   .outleticon{
     .wh(450px,450px);
     margin: 0 auto;
     margin-top: 129px;
     img{
      .wh(450px,450px)
     }
    }
   .prompt{
     margin: 0 auto;
     width: 650px;
     margin-top: 42px;
     line-height: 35px;
     text-align: left;
     span{
       margin: 0 20px;
       font-size: 32px;
       color: #585858;
       text-align: left;
       display: inline-block
     }
    }
    .nextbtn{
      .wh-l(520px,100px);
      margin: 0 auto;
      border-radius: 10px;
      background: #13af68;
      margin-top: 30px;
      span{
        font-size: 47px;
        color: #fff;
        vertical-align: middle;
      }
    }
    .nextDisable{
      background: #D6D6D6;
    }
    .light{
      .wh-l(414px, 40px);
      font-size: 25px;
      margin: 80px auto 0 auto;
      display: flex;
      justify-content: space-between;
      img{
        .wh(40px,40px);
        align-self: center;
      }
    }
    .slide-right-enter-active, .slide-right-leave-active{
      transition: all .3s cubic-bezier(.55,0,.1,1);
    }
    .slide-right-enter, .slide-right-leave-to{
      transform: translateX(-100%);
      opacity: 0;
    }
    .slide-left-enter-active, .slide-left-leave-active{
      transition: all .3s cubic-bezier(.55,0,.1,1);
    }
    .slide-left-enter, .slide-left-leave-to{
      transform: translateX(100%);
      opacity: 0;
    }
  }
</style>



